export default {
	data() {
		return {
			img_url:this.$img_url,
			sizeCalcState: false,
			tabScrollTop: 0,
			currentId: 1,
			currentName: '',
			flist: [],
			slist: []
		}
	},
	onLoad(){ 
		this.loadData() 
		this.tabbar = this.$api.CC_request.get_category_list().then(res => {
			    let list = res
				list.forEach(item=>{
					if(!item.pid){
						this.flist.push(item);  //pid为父级id, 没有pid或者pid=0是一级分类
					}else {
						this.slist.push(item); //2级分类
					}
				}) 					
				if(list[0]){
					this.currentName=list[0].cate_name;
					this.currentId = list[0].id;
				}
			})
	},
	methods: {
		loadData(){
			this.tabbar = this.$api.http.get('category/all_category').then(res => {
				let list = res.data
				list.forEach(item=>{
					if(item.pid == 0){
						this.flist.push(item);  //pid为父级id, 没有pid或者pid=0是一级分类
					}else {
						this.slist.push(item); //2级分类
					}
				}) 					
				if(list[0]){
					this.currentName=list[0].category_name;
					this.currentId = list[0].category_id;
				}
			})
		},
		//一级分类点击
		tabtap(item){
			if(!this.sizeCalcState){
				this.calcSize();
			}
			this.currentName=item.cate_name;
			this.currentId = item.id;
			//let index = this.slist.findIndex(sitem=>sitem.pid === item.id);
			//this.tabScrollTop = this.slist[index].top;
			this.slist = item.children
			console.log(this.slist)
		},
		//右侧栏滚动
		asideScroll(e){
			if(!this.sizeCalcState){
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item=>item.top <= scrollTop).reverse();
			if(tabs.length > 0){
				this.currentId = tabs[0].pid;
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize(){
			let h = 0;
			this.slist.forEach(item=>{
				let view = uni.createSelectorQuery().select("#main-" + item.id);
				view.fields({
					size: true
				}, data => {
					item.top = h;
					h += data.height;
					item.bottom = h;
				}).exec();
			})
			this.sizeCalcState = true;
		},
		navToList(sid){
			const cid=this.currentId
			uni.navigateTo({
				url: `/pages/extend-view/productList/productList?cid=${cid}&sid=${sid}`
			})
		}
	}
}
